import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Alert from "../components/Alert";
import CustomInput from "../components/CustomInput";
import NavBar from "../components/Navigation/NavBar";
import { listCategoryAction, addCategoryAction } from "../redux/slices/categoriesSlice"
import MainContainer from "../components/MainContainer";
import { setHeader } from "../redux/slices/headerSlice";
import { HOME_PATH } from "../utility/path";
import { getConstant, handleHeader } from "../services/functions";

const Category = ({ }) => {
  const [state, setState] = useState({ name: '' });
  const categoriesData = useSelector(state => state?.categories?.data);
  const authData = useSelector(state => state?.auth?.data);
  const dispatch = useDispatch();

  useEffect(() => {
    handleHeader({...getConstant("CATEGORY")},dispatch);
    
    let ignore = false;
    if (!ignore && !categoriesData.length) {
      dispatch(listCategoryAction({ sample: "request" }));
    }

    return () => { ignore = true }
  }, []);


  const handleClick = () => {
    console.log('handle click');
  }

  // handle field change
  const handleChange = input => e => {
    setState({ ...state, [input]: e.target.value.toLowerCase() });
  }

  const Continue = e => {
    e.preventDefault();
    //setIsShowInit(true);
    if (!state || !state.name) {
      return;
    }

    dispatch(addCategoryAction({ ...state }));
    setState({ ...state, name: "" });


    // setTimeout(() => {
    //   console.log('timeout')
    //   setIsShowInit(false);
    // }, 3000);
  }

  const [isShowInit, setIsShowInit] = useState(false);
  const handleClose = (e) => {
    e.preventDefault();
    setIsShowInit(false);
  };

  const [addNewCategory, setAddNewCategory] = useState(false);


  const renderForm = () => {
    return (
      <>
        <div className="form-check form-switch mb-3">
          <input className="form-check-input"
            type="checkbox" role="switch" id="flexSwitchCheckChecked"
            onChange={() => setAddNewCategory(!addNewCategory)} />
          <label className="form-check-label" htmlFor="flexSwitchCheckChecked">{addNewCategory ? 'Hide' : 'Add'} new category ?</label>
        </div>


        {addNewCategory ? (<>
          <CustomInput title="Add Category" id="categoryId" type="text" placeholder="Enter category" handleChange={handleChange} name="name" value={state.name} />
          <button className="btn btn-primary mb-3" onClick={Continue} disabled={!state.name}>Submit</button>
        </>) : ('')}



        {categoriesData.length > 0 && (<h3>Categories</h3>)}
        <ul className="list-scrollable">
          {categoriesData.map((ct) => (
            <li key={ct.id}>
              <div key={ct.id} className="row">
                <div className="col">{ct.name}</div>
                <div className="col">
                  <button className="btn btn-outline-secondary">Edit</button>
                  <button className="btn btn-outline-danger">Delete</button>
                </div>
              </div>
            </li>
          ))}
        </ul>
      </>
    );
  }

  return (
    <>
      <NavBar />
      <MainContainer>
        {renderForm()}
      </MainContainer>

    </>
  );
}

export default Category;
