import { useEffect, useState } from "react";
import {
  Form,
  useLoaderData,
  redirect,
  useNavigate,
  useLocation,
} from "react-router-dom";

import EnterMobile from './EnterMobile';
import EnterOtp from './EnterOtp';
import EnterName from './EnterName';
import EnterSetPassword from './EnterSetPassword';
import EnterPassword from './EnterPassword';
import { useDispatch, useSelector } from "react-redux";
import { logoutAction, previousAction, setNameAction, setPasswordAction, verifyMobileAction, verifyOtpAction, verifyPasswordAction } from "../redux/slices/authSlice";
import {listCategoryAction} from "../redux/slices/categoriesSlice";
import NavBar from "../components/Navigation/NavBar";
import { refreshProfileAction } from "../redux/slices/profilesSlice";
import { HOME_PATH, LOGOUT_PATH } from "../utility/path";

const Register = () => {
  
  const location = useLocation();
  
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //const users = useSelector(state => state?.auth);
  const authData = useSelector(state => state?.auth?.data);
  
  const [state, setState] = useState({step:1,mobile:authData?.mobile || "",otp:"",name:"",password:"",confirmPassword:"",address:'',pincode:'',category:'', fee:'',locationPathname:""});
  const [currentStep,setCurrentStep] = useState([1]);
  const [next,setNext] = useState(1);

  const previousStep = () =>{
   console.log("previous action called")
   dispatch(previousAction(currentStep[currentStep.length-1]));
   setCurrentStep(currentStep.filter((el)=> el!=currentStep[currentStep.length-1]));
  }

  const nextStep = () =>{
    console.log("next",authData?.nextStep,next)
    switch(next){
      case 1:
        setCurrentStep([1]);
        dispatch(verifyMobileAction({...state,locationPathname:location?.pathname}));
        return;
      case 2:
        setCurrentStep([1,2]);
        dispatch(verifyPasswordAction({...state,locationPathname:location?.pathname,deviceId:localStorage.getItem("deviceId")}));
        setState({ ...state, password: "" });
        return;
      case 3:
        let _currentStep = currentStep;
        _currentStep.push(3);
        setCurrentStep(_currentStep);
        dispatch(verifyOtpAction({...state,locationPathname:location?.pathname}));
        dispatch(listCategoryAction(authData));
        setState({ ...state, otp: "" });
        return;
      case 4:
        let __currentStep = currentStep;
        __currentStep.push(4);
        setCurrentStep(__currentStep);
        dispatch(setNameAction({...state,locationPathname:location?.pathname}));
        return;
      case 5:
        let ___currentStep = currentStep;
        ___currentStep.push(5);
        setCurrentStep(___currentStep);
        dispatch(setPasswordAction({...state,locationPathname:location?.pathname}));
        setState({ ...state, confirmPassword: "",password:"" });
        return;
      case 6:
        let ____currentStep = currentStep;
        ____currentStep.push(6);
        setCurrentStep(____currentStep);
        goto();
        return;
      default:
        setCurrentStep([1]);
        goto();
        return;
    }
  }

  // handle field change
  const handleChange = input => e => {
    e.preventDefault();
    setState({ ...state, [input]: e.target.value });
  }

  const handleChangeOtp = (input,value) => {
    setState({ ...state, [input]: value });
  }

  const renderForm = () => {
    switch(next){
      case 1:
        return (<EnterMobile nextStep={nextStep} handleChange={handleChange} state={state}/>)
      case 2:
        return (<EnterPassword nextStep={nextStep} previousStep={previousStep} handleChange={handleChange} state={state}/>)
      case 3:
        return (<EnterOtp nextStep={nextStep} previousStep={previousStep} handleChangeOtp={handleChangeOtp} state={state}/>)
      case 4:
        return (<EnterName nextStep={nextStep} previousStep={previousStep} handleChange={handleChange} state={state}/>)
      case 5:
        return (<EnterSetPassword nextStep={nextStep} previousStep={previousStep} handleChange={handleChange} state={state}/>)
      default:
        //do nothing  
    }
  }

  useEffect(()=>{
    console.log("mount11",authData,next)
    setNext((c) => 1);

    return ()=>{setNext(1);console.log("unmount11")};
  },[])

  
  useEffect(()=>{
    console.log('use effect',authData,next)
    if(location?.pathname == LOGOUT_PATH){
      dispatch(refreshProfileAction({}));
      dispatch(logoutAction({}));
      goto();
    }else if(authData?.isLoggedIn){
      goto();
    }

    setNext((c)=> authData?.nextStep || 1);
    
  },[authData?.nextStep]);


  const goto = () => {
    console.log("goto")
    return navigate(HOME_PATH);
  }
  
  

    return (
      <>
      <NavBar/>
      <div className="main-container">
        <div className="row">
          <div className="col-md-6 offset-md-3">
            {renderForm()}
          </div>
        </div>
      </div>
      </>
    );
}
  
 export default Register;
  
