import { useEffect, useState } from "react";
import CustomInput from "../components/CustomInput";
import OtpComponent from "../components/OtpComponent";
import { getConstant, handleHeader } from "../services/functions";
import { useDispatch } from "react-redux";
import H from "../components/H";

const EnterOtp = ({ nextStep, previousStep, handleChangeOtp, state }) => {
  const Continue = e => {
    e.preventDefault();
    nextStep();
  }

  
  const [showTransition,setShowTransition] = useState(false);
  const dispatch = useDispatch();

  useEffect(()=>{
    setShowTransition(true);
    handleHeader({...getConstant("VERIFY_OTP")},dispatch);
    return ()=>{setShowTransition(false)}
  },[]);

  return (
    <div className={`show-transition ${showTransition ? "show" : ""}`}>
      <H value={"Verify Otp"}/>
      {/* <CustomInput title="" id="otpId" type="text" placeholder="Enter otp" handleChange={handleChange} name="otp" value={state?.otp}/> */}
      <OtpComponent handleChangeOtp={handleChangeOtp}/>
      <button className="btn btn-primary" onClick={previousStep}>Back</button>
      <button className="btn btn-primary m-2" onClick={Continue} disabled={!state.otp || state.otp.length!=6}>Submit</button>
    </div>
  );
}

export default EnterOtp;
