import { useEffect, useState } from "react";
import CustomInput from "../components/CustomInput";
import { getConstant, handleHeader } from "../services/functions";
import { useDispatch } from "react-redux";
import H from "../components/H";

const EnterSetPassword = ({nextStep, previousStep, handleChange, state }) =>{
  const Continue = e => {
    e.preventDefault();
    nextStep();
  }

  
  const [showTransition,setShowTransition] = useState(false);
  const dispatch = useDispatch();

  useEffect(()=>{
    setShowTransition(true);
    handleHeader({...getConstant("SET_PASSWORD")},dispatch);
    return ()=>{setShowTransition(false)}
  },[]);

  return (
    <div className={`show-transition ${showTransition ? "show" : ""}`}>
      {/* <h3>Set Password</h3> */}
      <H value={"Set new password"}/>
      <CustomInput title="" id="passwordId" type="password" placeholder="Enter password" handleChange={handleChange} name="password" value={state?.password}/>
      <CustomInput title="" id="confirmPasswordId" type="password" placeholder="Enter confirm password" handleChange={handleChange} name="confirmPassword" value={state?.confirmPassword}/>

      <button className="btn btn-primary" onClick={previousStep}>Back</button>
      <button className="btn btn-primary m-2" onClick={Continue} disabled={!state.confirmPassword || !state.password || (state.confirmPassword != state.password)}>Submit</button>
    </div>
  );
  }
  
  export default EnterSetPassword;
  
