import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import NavBar from "../components/Navigation/NavBar";
import MainContainer from "../components/MainContainer";
import { setHeader } from "../redux/slices/headerSlice";
import { HOME_PATH, SUBSCRIPTION_PATH } from "../utility/path";
import { displayDateTime, getConstant, handleHeader } from "../services/functions";


const SubscriptionDetails = ({ }) => {
    const payment = useSelector((state) => state.payment.data);
    const authData = useSelector((state)=> state.auth.data);
    const dispatch = useDispatch();

    useEffect(() => {
        handleHeader({...getConstant("SUBSCRIPTION_DETAILS")},dispatch);

        return () => { dispatch(setHeader({ gotoName: "Subscription", gotoAction: SUBSCRIPTION_PATH })); }
    }, []);

    return (
        <>
            <NavBar />
            <MainContainer>
                <p><b>Request ID:</b> {payment?.payment_request?.id}</p>
                <p className="capitalize"><b>Name:</b> {payment?.payment_request?.buyer_name}</p>
                <p><b>Plan:</b> {payment?.payment_request?.purpose}</p>
                <p><b>Amount:</b> {payment?.payment_request?.amount + " "+authData?.countryCode}</p>
                <p><b>Status:</b> {payment?.payment_request?.status}</p>
                <p><b>Date:</b> {displayDateTime(payment?.payment_request?.modified_at)}</p>
            </MainContainer>
        </>
    );
}

export default SubscriptionDetails;
